<template>
  <div>
    <div class="wrapper-burger">
      <button id="button" type="button" @click="menuView"></button>
      <div class="burger"></div>
    </div>
    <div id="menu" class="menu-block">
      <ul class="menu-list">
        <li class="list-item">
          <router-link to="/about" active-class="active">Обо мне</router-link>
        </li>
        <li class="list-item">
          <router-link to="/portfolio" active-class="active">Портфолио</router-link>
        </li>
        <li class="list-item">
          <router-link to="/contacts" active-class="active">Контакты</router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
 export default {
   name: 'menu',
   methods: {
     menuView() {
       let
         burger = document.querySelector(".burger"),
         menu = document.getElementById("menu");
       function displayNone() {
         menu.classList.add("menu-none");
       }
       if (burger.classList.contains("exit")) {
         burger.classList.remove("exit");
         menu.classList.add("menu-none-transition");
         setTimeout(displayNone, 450);
       } else {
         burger.classList.add("exit");
         menu.classList.remove("menu-none");
         menu.classList.remove("menu-none-transition");
         menu.classList.add("menu-active");
       }
     }
   }
 }
</script>

<style scoped>
  /***Burger***/
  .wrapper-burger {
    position: absolute;
    display: flex;
    align-items: center;
    top: 50px;
    right: 100px;
    width: 40px;
    height: 100px;
    z-index: 250;
  }

  button {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    background: transparent;
    border: 0;
    outline: 0;
    cursor: pointer;
  }

  .burger {
    position: relative;
    display: block;
    width: 100px;
    height: 3px;
    background: #000;
    transition: transform .4s ease-in-out, background .1s ease-in-out, opacity .1s ease-in-out;

  }

  .burger::before,
  .burger::after {
    content: "";
    position: absolute;
    display: block;
    top: 10px;
    right: 0;
    width: 85%;
    height: 100%;
    background: #000;
    transition: top .2s ease-in-out, transform .2s ease-in-out, width .3s ease-in-out,background .1s ease-in-out;

  }

  .burger::after {
    top: -10px;
    width: 68%;
  }

  button:hover + .burger,
  button:hover + .burger::after,
  button:hover + .burger::before {
    background: blue;
  }

  button:active + .burger {
    color: #000;
    opacity: .6;
  }

  .exit {
    transform: rotate(135deg);
  }

  .exit::before {
    top: 0;
    width: 100%;
    transform: rotate(90deg);
  }

  .exit::after {
    top: 0;
    width: 0;
    opacity: 0;
  }

  /***Menu***/
  .menu-block {
    position: absolute;
    top: 0;
    right: 0;
    display: none;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background: #fff;
    border: 1px solid rgba(0,0,0,.1);
    transition: opacity .8s ease-in-out;
    animation-name: viewBlock;
    animation-duration: .4s;
    animation-timing-function: ease-in-out;
    z-index: 200;
  }

  .menu-list {
    list-style: none;
  }

  .list-item {
    text-align: right;
  }

  .list-item a{
    display: inline-block;
    padding: 10px 0;
    text-transform: uppercase;
    font-size: 2rem;
    text-decoration: none;
    font-weight: 900;
    color: #000;
    transition: transform .2s ease-in-out, color .2s ease-in-out, opacity .1s ease-in-out;
    animation-name: viewLink;
    animation-duration: .71s;
    animation-timing-function: ease-in-out;
  }

  .active {
    color: rgba(0,0,0,.5) !important;
  }
  .active:hover {
    color: blue !important;
  }

  .list-item:nth-child(2) a {
    animation-duration: 0.69s;
  }

  .list-item:nth-child(3) a {
    animation-duration: 0.67s;
  }

  .list-item a:hover {
    transform: scale(1.01) translate(5px, -3px);
    color: blue;
  }

  .list-item a:active {
    opacity: .6;
    transform: scale(.99);
    transition: transform .1s ease-in-out;
  }


  /***Animation***/
  @keyframes viewLink {
    0% {
      opacity: 0;
      transform: translateX(250px);
    }
    25% {
      opacity: 0;
      transform: translateX(200px);
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }


  @keyframes viewBlock {
    0% {
      width: 0;
      height: 0;
      background: transparent;
      transform: translate(150vw,-100vh);
    }
    30% {
      background: transparent;
    }
    70% {
      width: 100vw;
      height: 100vh;
      transform: translate(10vw,-10vh);
    }
    100% {
      width: 100vw;
      height: 100vh;
      background: #fff;
      transform: translate(0,0);
    }
  }

  @keyframes menuNone {
    0% {
      width: 100vw;
      height: 100vh;
      transform: translate(0,0);
    }
    95% {
      background: transparent;
    }
    100% {
      width: 0;
      height: 0;
      background: transparent;
      transform: translate(100vw,-100vh) scale(.3);
    }
  }

  /***Class for JS***/
  .menu-active {
    display: flex;
  }

  .menu-none-transition {
    transition: color 0.5s ease-in-out;
    animation: menuNone .7s ease-in-out;
    animation-fill-mode: forwards;
  }

  .menu-none {
    display: none;
  }
</style>


