<template>
  <div class="hello">
    <app-menu></app-menu>
    <h1>Анна Пацкан</h1>
    <p>Привет! Сейчас ты находишься
      на сайте одного талантливого
      и креативного художника.</p>
    <router-link to="/portfolio" class="label-back" active-class="active">В портфолио<span class="arrow"></span></router-link>
  </div>
</template>

<script>
  import Menu from '../components/menu.vue'
export default {
  components: {
    'app-menu': Menu
  },
  name: 'home'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .label-back {
    position: relative;
    display: inline-block;
    padding: 10px;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: 900;
    font-size: 22px;
    color: #000;
    transition: opacity .05s ease-in-out, transform .1s ease-in-out, color .2s ease-in-out;
  }
  .label-back:hover {
    color: blue;
  }

  .label-back:active {
    opacity: .6;
    transform: scale(.95);
    transition: transform .1s ease-in-out;
  }

  .arrow {
    position: absolute;
    top: 24px;
    right: -20px;
    display: block;
    width: 26px;
    height: 1px;
    background: #000;
    transition: background .1s ease-in-out;
  }

  .arrow::before,
  .arrow::after {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    width: 40%;
    display: block;
    height: 1px;
    background: #000;
    transform: translate(1px, -3.5px) rotate(45deg);
    transition: width .15s ease-in-out, transform .2s ease-in-out;
  }

  .arrow::after {
    transform: translate(1px, 3.5px) rotate(-45deg);
  }

  .label-back:hover > .arrow {
    background: transparent;
    transform: translateX(10px);
  }

  .label-back:hover > .arrow::before,
  .label-back:hover > .arrow::after {
    width: 60%;
    transform: translate(2px, -5.5px) rotate(45deg);
  }

  .label-back:hover > .arrow::after {
    transform: translate(2px, 5.5px) rotate(-45deg);
  }
</style>



