<template>
  <div>
    <app-menu></app-menu>
    <h1>Портфолио</h1>
    <div class="gallery-wrapper">
      <div class="gallery" id="gallery" @mouseover="slides">
        <div class="picture-item"><img src="static/img/1.jpg" alt=""></div>
        <div class="picture-item"><img src="static/img/2.jpg" alt=""></div>
        <div class="picture-item"><img src="static/img/3.jpg" alt=""></div>
        <div class="picture-item"><img src="static/img/4.jpg" alt=""></div>
        <div class="picture-item"><img src="static/img/5.jpg" alt=""></div>
        <div class="picture-item"><img src="static/img/6.jpg" alt=""></div>
        <div class="picture-item"><img src="static/img/7.jpg" alt=""></div>
        <div class="picture-item"><img src="static/img/logo.png" alt=""></div>
        <div class="picture-item"><img src="static/img/logo.png" alt=""></div>
        <div class="picture-item"><img src="static/img/logo.png" alt=""></div>
        <div class="picture-item"><img src="static/img/logo.png" alt=""></div>
        <div class="picture-item"><img src="static/img/logo.png" alt=""></div>
        <div class="picture-item"><img src="static/img/logo.png" alt=""></div>
        <div class="picture-item"><img src="static/img/logo.png" alt=""></div>
        <div class="picture-item"><img src="static/img/logo.png" alt=""></div>
        <div class="picture-item"><img src="static/img/logo.png" alt=""></div>
        <div class="picture-item"><img src="static/img/logo.png" alt=""></div>
        <div class="picture-item"><img src="static/img/logo.png" alt=""></div>
        <div class="picture-item"><img src="static/img/logo.png" alt=""></div>
        <div class="picture-item"><img src="static/img/logo.png" alt=""></div>
      </div>
    </div>
    <div class="filter-wrapper">
      <button class="filter-label">Фильтры</button>
      <div class="filter">
        <ul class="filter-list">
          <li class="filter-item"><a href="#">Портреты</a></li>
          <li class="filter-item"><a href="#">Пейзажи</a></li>
          <li class="filter-item"><a href="#">Графика</a></li>
          <li class="filter-item"><a href="#">Иллюстрации</a></li>
          <li class="filter-item"><a href="#">Скетчи</a></li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import Menu from '../components/menu.vue'

export default {
  components: {
    'app-menu': Menu
  },
  methods: {
    slides () {
      const slider= document.querySelector('.gallery');
      let isDown = false;
      let startX;
      let scrollLeft;

      slider.ondragstart = function() {
        return false;
      };

      slider.addEventListener('mousedown', (e) => {
        isDown = true;
        slider.classList.add('active');
        startX = e.pageX - slider.offsetLeft;
        scrollLeft = slider.scrollLeft;
      });
      slider.addEventListener('mouseleave', () => {
        isDown = false;
        slider.classList.remove('active');
      });
      slider.addEventListener('mouseup', ()=>  {
        isDown = false;
        slider.classList.remove('active');
      });
      document.addEventListener('mousemove', (e) => {
        if(!isDown) return;
        e.preventDefault();
        const x = e.pageX - slider.offsetLeft;
        const walk = (x- startX) * 2;
        slider.scrollLeft = scrollLeft - walk;
      });
    }
  },
  name: 'portfolio'
}
</script>

<style scoped>
  /***Filter label***/
  .filter-label {
    position: absolute;
    bottom: 40px;
    right: 100px;
    display: inline-block;
    padding: 10px;
    text-transform: uppercase;
    font-family: 'Geometria', 'Avenir', Helvetica, Arial, sans-serif;
    font-weight: 900;
    font-size: 22px;
    color: #000;
    background: transparent;
    border: none;
    outline: none;
    transition: color .25s ease-in-out;
  }

  .filter-label::before,
  .filter-label::after {
    position: absolute;
    top: 24px;
    right: -12px;
    content: "";
    width: 15px;
    height: 1px;
    background: #000;
    transition: width .2s ease-in-out , height .2s ease-in-out , transform .25s ease-in-out;
  }

  .filter-label::after {
    transform: rotate(90deg);
  }

  .filter-label:hover {
    color: blue;
  }

  .filter-label:hover::before,
  .filter-label:hover::after {
    width: 10px;
    opacity: 1;
    transform: translate(5px, -3.2px) rotate(45deg);
  }

  .filter-label:hover::after {
    transform: translate(5px, 3.2px) rotate(-45deg);
  }


  /***Filter block***/
  .filter {
    position: absolute;
    top: 0;
    right: 0;
    padding: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 320px;
    height: 100vh;
    background: #fff;
    border: 1px solid rgba(0,0,0,.1);
    transform: translateX(320px);
    transition: transform .5s ease-in-out;
    transition-delay: 0.15s;
    z-index: 250;
  }

  .filter-list {
    list-style: none;
  }

  .filter-item {
    text-align: left;
  }

  .filter-item a{
    display: inline-block;
    padding: 10px;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: 900;
    font-size: 22px;
    color: #000;
    transition: transform .2s ease-in-out, color .2s ease-in-out, opacity .1s ease-in-out;
  }

  .filter-item a:hover {
    transform: scale(1.01) translate(5px, -3px);
    color: blue;
  }

  .filter-item a:active {
    opacity: .6;
    transform: scale(.99);
    transition: transform .1s ease-in-out;
  }

  .filter-label:hover + .filter {
    transform: translateX(0px);
  }

  .filter:hover {
    transform: translateX(0px);
  }

  .gallery-wrapper {
    position: relative;
    width: 100vw;
    overflow: hidden;
  }

  .gallery-wrapper::before,
  .gallery-wrapper::after {
    position: absolute;
    content: "";
    height: 100%;
    width: 150px;
    top: 0;
    left: 0;
    background: linear-gradient(to right, #fff,transparent);
    z-index: 10;
  }
  .gallery-wrapper::after {
    left: calc(100vw - 150px);
    transform: rotate(180deg);
  }

  .gallery {
    display: block;
    padding: 0 90px;
    width: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    user-select: none;
    transition: all 0.3s;
    will-change: transform;
    position: relative;
    cursor: pointer;
  }
  .gallery::-webkit-scrollbar {
    width: 0;
  }
  .gallery {
    -ms-overflow-style: none;
  }

  .picture-item {
    display: inline-flex;
    height: 180px;
    width: auto;
    margin: 20px 30px;
    background: lightgray;
  }

  .picture-item img {
    height: 100%;
    width: auto;
  }

  .active {
    transform: scale(.98);
    cursor: grabbing;
  }
</style>


